import React, { useState } from 'react';
import "../App.css";

function Company() {
  let [idx, setIdx] = useState(0);
  let contents = [<Con1 />, <Con2 />, <Con3 />];

  function company(e) {
    let num = e.target.dataset.num;
    let li = document.querySelectorAll('.Company li');
    
    li.forEach(function (el, k) {
      // [].map.call(li,function(el,k){
      if (num == k) {
        el.classList.add('active');
      } else {
        el.classList.remove('active');
      }
    });

    setIdx(num);
  }
  return (
    <div className="Company">
      <div className="background_1">
      <img src='img/company/ba1.jpg' />
      <h2>COMPANY</h2>
      </div>
      <ul>
        <li onClick={company} data-num="0" className="active">소개</li>
        <li onClick={company} data-num="1">3대 이너셔티브</li>
        <li onClick={company} data-num="2">Commitment</li>
      </ul>
      {contents[idx]}

    </div>
  );
}

function Con1() {
  return (
    <div className="info_1">
      <img src="img/company/s_1.jpg" />
      <div className="info_1_title">
      <h2>Manifesto</h2>
      <p>아모레퍼시픽은 믿습니다.</p>

      <p>모든 여성들이 무한한 능력과 끊임없는 열정으로

        자신과 가족, 사회와 인류를 위해
        풍요롭고 아름다운 삶을 가꾸어 왔으며,
        앞으로도 세상을 더 아름답게 만들 수 있다는 것을.

      앞으로도 세상을 더 아름답게 만들 수 있다는 것을.</p>
      <p className="color">여성의 삶이 아름다워졌을 때,
      비로소 이 세상 또한 아름다워질 수 있다는 것을 믿기에</p>
      <p>아모레퍼시픽은 여성의 삶을 아름답게 하는 것을 우리의 소명으로 생각합니다.</p>
      <p>아모레퍼시픽은 A MORE Beautiful World라는 비전 아래
      모든 여성들이 저마다 꿈꾸는 삶을 누리며
      더 아름다운 세상을 만들어 갈 수 있도록
      여성들의 아름다운 삶에 기여하고자 합니다.</p>
      </div>
    </div>
  )
}
function Con2() {
  return (
    <div className="info_2">
      <div className="info_2-top">
        <div className="info_2-top-1">
        <div className="info_2-top-1_font">
      <strong>Our Initiatives</strong>
      <p className="text">아모레퍼시픽은 더 아름다운 세상을 위해 여성, 자연생태, 문화 3대 이니셔티브를 중심으로<br />사회에 긍정적 영향을 미치는 다양한 사회공헌 프로그램을 운영하고 있습니다.
      </p>
      </div>
      </div>
      </div>
      <div className="info_2-mid">
      <a href="#">
        <span>Women</span>
        <p>여성의<br />
        건강과 웰빙,<br />
        역량 강화</p>
      </a>
      <a href="#">
        <span>Ecosystem</span>
        <p>자연의<br />
        건강한 아름다움과<br />
        공존하는 삶</p>
      </a>

      <a href="#">
        <span>Culture</span>
        <p>문화의<br />
        가치 발견과<br />
        공유</p>
      </a>
      </div>

      <div className="info_2-bottom">
        <img src="img/company/s_2-1.jpg" alt="" />
        <img src="img/company/s_2-2.jpg" alt="" />
        <img src="img/company/s_2-3.jpg" alt="" />
      </div>

    </div>
  )
}
function Con3() {
  return (
    <div className="info_3">
      <div className="info_3_1-ba">
        <img src="./img/company/s_3.jpg" alt="" />
      </div>
      <div className="info_3_1">
        <div className="info_3_1-top">
          <strong><em>2020년,<br />
        20만명의 여성이</em><br /> 아름답게 피어나다</strong>
        </div>
        <div className="info_3_1-bottom">
          <p>‘20 by 20' Commitment는 2020년까지 여성 20만 명의 건강과 웰빙, 경제적 역량 강화를 통해 여성의 삶을 아름답게 하는 데
          기여하겠다는 아모레퍼시픽의 약속입니다. 아모레퍼시픽은 여성과 함께 성장해온 기업으로서 여성의 삶을 아름답게 하는 것이
         곧 더 아름다운 세상을 위한 것이라 믿고 있습니다.</p>
        </div>
      </div>
     
      <div className="info_3_2">
      <div className="info_3_2-ba">
        <img src="./img/company/s_3_2.jpg" alt="" />
      </div>

        <div className="info_3_2-bottom">
          <h2>모두가 차별 없이<br />
          아름다움을 즐기는 삶</h2>

          <p>한국뿐만 아니라 중국, 싱가포르, 홍콩, 대만, 베트남 등 아모레퍼시픽 법인이 위치한 국가에서 여성의 삶에 아름답게 하기 위해 노력하고 있으며, UN에서 진행하고 있는 Every Woman Every Child라는 글로벌 무브먼트에 동참하여 우리의 비전을 더 많은 사람들과 공유하고, 글로벌 기업 시민으로서 책임을 다하고자 합니다.
          </p>
          <p>Every Woman Every Child는 여성, 아동, 청소년의 건강 및 웰빙을 위해 2010년부터 UN이 진행하는 글로벌 무브먼트입니다. 정부, 시민사회, 기업 등이 참여하고 있으며 2015 년 이래 62 개국 150 여 개의 이해 관계자들이 EWEC 무브먼트에 참여하고 있습니다.</p>
        </div>
 
      </div>

      <div className="info_3_3">
      <div className="info_3_3-ba">
        <img src="./img/company/s_3_3.jpg" alt="" />
      </div>
      <div className="info_3_3-top">
          <span>‘20 by 20’<br />Commitment</span>
          <p>20 by 20 Commitment를 이행하기 위해 매년 최소 70억원을<br /> 지원하여 5만명의 여성의 삶을 아름답게 하는데 기여하겠습니다.
          </p>
        </div>
        </div>
    </div>
  )
}
export default Company;