import React, { useState } from 'react';
import "../About.css";

function About() {
  let [idx, setIdx] = useState(0);
  let contents = [<Con1 />, <Con2 />];

  function about(e) {
    let num = e.target.dataset.num;
    let li = document.querySelectorAll('.About li');
    
    li.forEach(function (el, k) {
      // [].map.call(li,function(el,k){
      if (num == k) {
        el.classList.add('active');
      } else {
        el.classList.remove('active');
      }
    });

    setIdx(num);
  }

  return(
    <div className="About">
    <div className="background_1">
    <img src="img/about/ba1.jpg" />
    <h2>사회 기업공헌</h2>
    </div>
    <ul>
      <li onClick={about} data-num="0" className="active">메이크업 유어 라이프</li>
      <li onClick={about} data-num="1">밋 유어 드림</li>
    </ul>
    {contents[idx]}

  </div>
  );
}
function Con1() {
  return (
    <div className="app_1">
      <div className="app_1-1">
        <div className="app_1-ba">
          <img src="img/about/s_1.jpg" alt="" />
        </div>

        <div className="app_1-1-detail">
          <div className="app_1-1-detail_1">
              <strong>
                  유방암으로부터<br /> 자유로운<br />
                  건강한 삶을 응원합니다</strong>
              <p>핑크리본 캠페인은 유방암 인식개선과 조기 발견의 중요성을 통해 건강한 삶을 지원하고 있습니다. 대중이 참여할 수 있는 다양한 활동을 통해 전 세대가 자신의 몸에 대한 관심과 건강을 돌보는 실천을 강조하고 있습니다.
              특히 2020년 올해는 캠페인 20주년 입니다. 한국유방건강재단의 설립과 함께 유방암 조기발견의 인식 향상에 기여해 온 아모레퍼시픽의 핑크리본 캠페인은 최근 유방암의 발병률이 지속적으로 증가함에 따라 유방암 정기검진을 통한 조기발견의 중요성과 함께 근본적인 유방암 예방을 위해 적절한 운동과 균형 있는 식생활, 정기적인 검진(유방암 선별검사, 자가검진, 가족력 검사)을 통해 건강한 생활 습관을 알리는 캠페인으로 거듭나고자 합니다.</p>
          </div>
          <div className="app_1-1-detail_2">
             <strong>유방건강을 지키는 생활<br /> 속 행동 지침 3가지</strong>
              <p>한국 등 아시아환자의 유방암은 서구권의 유방암과는 차이가 있습니다. 서구권은 85%가 폐경 후에 발생하는 반면 한국 등 아시아환자는 50%가 폐경 전에 발생합니다. 하지만 계속 되는 서구화된 식생활 등으로 유방암 발병 위험인자에 노출이 많아 지면서 서구권 유방암 형태로 변화하고 있습니다. 계속 발병률이 높아지는 유방암을 대비하여 유방건강을 지키는 생활 속 행동 지침 3가지를 실천해 보세요.</p>

              <p>“정기적인 검진”<br />
                  “규칙적인 운동”<br />
                  “올바른 식습관”</p>
          </div>
        </div>
      </div>


      <div className="app_1-2">
          <div className="app_1-2_1">
            <div className="app_1-2_1-ba"><img src="img/about/s_2.jpg" alt="" /></div>
            <div className="app_1-2_1-detail">
              <strong>러닝 축제</strong>
                <p>대중의 참여로 함께 만들어가는 러닝 페스티벌은 일상 속에서 여성 건강에 대한 관심과 유방, 자궁암 관련 검진을 독려하며 조기 발견의 중요성을 알리는 프로그램 입니다. 대표적으로 한국에서 진행 되는 핑크런은 매년 전국 5개 지역에서 개최되고, 대회 참가비 전액(10,000원)은 한국유방건강재단에 기부되어 유방암 환자의 수술비 지원 및 검진 지원에 사용됩니다.</p>
                <div className="app_1-2_1_a">
                  <a href="https://www.instagram.com/accounts/login/?next=/pink_runner/" target="_blank">핑크런 신청하기</a>
                </div>
            </div>
          </div>
          <div className="app_1-2_2">
            <div className="app_1-2_2-ba"><img src="img/about/s_3.jpg" alt="" /></div>
            <div className="app_1-2_2-detail">
              <strong>자가 검진 교육</strong>
                <p>유방건강강좌 ‘핑크투어’는 조기 발견을 실천할 수 있도록 유방 자가 검진 방법을 알려주는 찾아가는 유방건강교육 프로그램입니다. 2001년부터 2019년까지 전국에서 약 1,500회에 걸쳐 37만 명의 일반인이 유방 자가 검진 및 생활 속 유방건강 강좌를 수강하였습니다. 핑크투어는 대표적인 유방건강 교육 프로그램으로 자리매김하고 있습니다.</p>
                <div className="app_1-2_2_a">
                  <a href="http://www.kbcf.or.kr/index.do" target="_blank">핑크투어 신청하기</a>
                </div>
            </div>
          </div>

      </div>
    </div>
  )
}
function Con2() {
  return (
    <div className="app_2">
      <div className="app_2-1">
        <div className="app_2-ba">
          <img src="img/about/s_4-1.jpg" alt="" />
        </div>

        <div className="app_2-1-detail">
          <div className="app_2-1-detail_1">
              <strong>          
                스스로를 아름답게<br />
                가꾸는 노하우</strong>
              <p>메이크업 유어 라이프는 항암 치료로 인한 갑작스러운 외모 변화로 힘들어하는 여성 암 환자들에게 ‘스스로를 아름답게 가꾸는 노하우’를 전수하는 캠페인입니다. 더 예뻐지기 위함이 아니라, 피부관리, 메이크업 등의 외모 관리를 통해 본래 그들이 누리던 기본적인 일상생활을 가능할 수 있게 하기 위해서 입니다.</p>
          </div>
          <div className="app_2-1-detail_2">
             <strong>메이크업이 주는<br />
              심리의 변화</strong>
              <p>여성 암 환자는 일반 여성보다 체형 변화에 대한 스트레스가 2배 높고, 탈모의 경우 3배, 피부 변화에 대한 스트레스는 2배 더 높다고 합니다. 이러한 스트레스는 수술 종료 12개월 이후에도 35% 이상의 환자들에게 지속되는 커다란 고통입니다. 유방암 전문의인 대림성모병원 김성원 원장이 발표한 연구 결과(메이크업 교육 프로그램이 유방암 환자에게 미치는 심리적 효과 연구 논문(공동연구자 분당 서울대학교병원 김정현, 박혜연 교수)에 따르면, 실제로 메이크업 유어 라이프에 참여했던 환자들의 경우 스트레스 지수가 약 17% 감소하고, 암에 대한 회피적 대응 정도도 12% 감소하는 등 긍정적인 심리 변화를 보인 것으로 나타났습니다.</p>

              <p>아모레퍼시픽은 메이크업 유어 라이프 캠페인을 통해 <span>한국 본사, 중국법인, 싱가포르 법인, 대만 법인, 베트남 법인, 홍콩 법인, 태국 법인, 말레이시아 법인, 인도네시아 법인 등</span> 아시아를 넘어, 전 세계 고객과 사회에 기여하기 위한 노력을 이어갈 것입니다.
            </p>
          </div>
        </div>
      </div>


      <div className="app_2-2">
            <div className="app_2-2_1-ba"><img src="img/about/s_5.jpg" alt="" /></div>
          <div className="app_2-2_2">

            <div className="app_2-2_2-detail">
            <strong>캠페인 참가 신청</strong>
                <p>현재 항암 치료를 받고 있거나 암 치료 후 2년 이내의 환자라면 누구나 참석 가능합니다. 항암 치료 과정 중 발생하는 피부 변화에 대해 알아보고 어떻게 대응하면 좋을지 ‘피부관리법’ 교육이 진행됩니다. 추가로 피부 변화를 고려한 메이크업 교육도 함께 진행됩니다. 캠페인은 날씨를 고려하여 매년 상반기(5월~6월), 하반기(9월~11월)로 나뉘어 운영되며 전국 병원 내에서 진행됩니다. 교육은 90분으로 구성되어 진행되며 참가 신청은 온라인 접수를 통해서 할 수 있습니다.</p>
                <div className="app_2-2_2_a">
                  <a href="https://www.apgroupcsr.com/activity/make/hospital/activity_make_hospital_reg.do?i_sFlagSSLYn=Y" target="_blank">신청하기</a>
                </div>
       


                <p>메이크업 유어 라이프의 2020년 상반기 캠페인은 ‘사회적 거리두기’ 동참을 위해 온라인으로 진행됩니다. 코로나19 이후 암환자에게 필요해진 마스크 메이크업 이나 실내 운동법 등의 콘텐츠와 암환자 레시피, 암 건강 관련 콘텐츠를 제공하고 있습니다. 해당 콘텐츠를 구독하고 싶은 암환자 및 가족들은 카카오톡 채널(메이크업유어라이프) 친구추가 후 확인 가능합니다.</p>
                <div className="app_2-2_2_a">
                  <a href="https://pf.kakao.com/_xhnqfj" target="_blank">신청하기</a>
                </div>
            </div>
          </div>

      </div>
    </div>
  )
}

export default About;