import React,{ useState } from 'react';
import Company from './component/Company';
import About from './component/About';
import Home from './component/Home';
import './App.css';

function App() {
  let navArr = {
    'Home' :<Home />,
    'Company':<Company />,
    'About':<About />
  
  };  
  let [cpnt,setCpnt]= useState(navArr['Home']);
  function nav(e){
    e.preventDefault();
    var menuTxt = e.target.textContent;
    let num = e.target.dataset.num;
    setCpnt(navArr[menuTxt]);

    var header = document.querySelector('header');
    if(num==0){
      header.classList.add('active');
    }else{
      header.classList.remove('active');
    }

    let a = document.querySelectorAll('.nav a');
    a.forEach(function(el,k){
        if(num == k){
          el.classList.add('active');
        }else{
          el.classList.remove('active');
        }
    });

  }
  return(
    <>
      <header className="active">
        <h2><img src="./img/logo.png" /></h2>
        <nav onClick={nav} className="nav">
          <a data-num="0" className="active">Home</a>
          <a data-num="1">Company</a>
          <a data-num="2">About</a>    
        </nav>
      </header>
      <main> 
        {cpnt}
      </main>
    </>
  );
}

export default App;

